// App.js
import React, { useState } from 'react';
import Canvas from './Canvas';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { v4 as uuidv4 } from 'uuid';

const App = () => {
  const [cards, setCards] = useState([
    {
      id: uuidv4(),
      type: 'customNode',
      data: { label: 'Sample text for card 1. This is a very long text to demonstrate the show more functionality.' },
      position: { x: 100, y: 100 },
    },
  ]);

  const moveCard = (id, position) => {
    setCards((prevCards) =>
      prevCards.map((card) =>
        card.id === id ? { ...card, position } : card
      )
    );
  };

  const addCard = () => {
    const newCard = {
      id: uuidv4(),
      type: 'customNode',
      data: { label: `New Card with a lot of text that needs to be shown more when the user clicks on the button.` },
      position: { x: 200, y: 200 }, // Default position for new card
    };

    setCards((prevCards) => {
      console.log('Adding new card:', newCard);
      return [...prevCards, newCard];
    });
  };
  console.log('Current cards:', cards);

  return (
    <DndProvider backend={HTML5Backend}>
      <div style={{ padding: '10px' }}>
        <button onClick={addCard}>Add Card</button>
      </div>
      <Canvas cards={cards} onCardMove={moveCard} />
    </DndProvider>
  );
};

export default App;
