import React from 'react';
import { Handle } from 'react-flow-renderer';
import { ResizableBox } from 'react-resizable';
import 'react-resizable/css/styles.css';

const CustomNode = ({ data, handleShowMore }) => {
  const [expanded, setExpanded] = React.useState(false);

  const text = data.label || 'No label'; // Default text if label is undefined

  return (
    <ResizableBox
      width={200}
      height={100}
      minConstraints={[100, 50]}
      maxConstraints={[600, 400]}
      style={{
        padding: '10px',
        border: '1px solid #ddd',
        borderRadius: '5px',
        background: '#fff',
        position: 'relative',
        overflow: 'hidden',
      }}
    >
      <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <p>
          {expanded ? text : `${text.substring(0, 50)}...`}
          <button onClick={() => setExpanded(!expanded)} style={{ marginLeft: '5px' }}>
            {expanded ? 'Show Less' : 'Show More'}
          </button>
          <button onClick={() => handleShowMore(text)} style={{ marginLeft: '5px' }}>
            More Details
          </button>
        </p>
        <Handle
          type="source"
          position="right"
          style={{ 
            top: '50%', 
            right: -10, 
            transform: 'translateY(-50%)', 
            background: '#555',
            width: '10px',  // Ensure width is visible
            height: '10px', // Ensure height is visible
          }}
        />
        <Handle
          type="target"
          position="left"
          style={{ 
            top: '50%', 
            left: -10, 
            transform: 'translateY(-50%)', 
            background: '#555',
            width: '10px',  // Ensure width is visible
            height: '10px', // Ensure height is visible
          }}
        />
        <Handle
          type="source"
          position="bottom"
          style={{ 
            bottom: -10, 
            left: '50%', 
            transform: 'translateX(-50%)', 
            background: '#555',
            width: '10px',  // Ensure width is visible
            height: '10px', // Ensure height is visible
          }}
        />
        <Handle
          type="target"
          position="top"
          style={{ 
            top: -10, 
            left: '50%', 
            transform: 'translateX(-50%)', 
            background: '#555',
            width: '10px',  // Ensure width is visible
            height: '10px', // Ensure height is visible
          }}
        />
      </div>
    </ResizableBox>
  );
};

export default CustomNode;
