import React, { useCallback, useEffect, useMemo } from 'react';
import ReactFlow, {
  addEdge,
  Background,
  MiniMap,
  Controls,
  useNodesState,
  useEdgesState,
  ReactFlowProvider,
} from 'react-flow-renderer';
import CustomNode from './CustomNode';
import Popup from './Popup';

const Canvas = ({ cards, onCardMove }) => {
  const [nodes, setNodes, onNodesChange] = useNodesState(cards);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [popupContent, setPopupContent] = React.useState(null);
  const [isPopupOpen, setIsPopupOpen] = React.useState(false);

  const onConnect = useCallback(
    (params) => setEdges((eds) => addEdge(params, eds)),
    [setEdges]
  );

  const onNodeDragStop = useCallback(
    (event, node) => {
      const updatedNodes = nodes.map((n) =>
        n.id === node.id ? { ...n, position: node.position } : n
      );
      setNodes(updatedNodes);
      onCardMove(node.id, { x: node.position.x, y: node.position.y });
    },
    [nodes, setNodes, onCardMove]
  );

  const handleShowMore = useCallback((content) => {
    setPopupContent(content);
    setIsPopupOpen(true);
  }, []);

  const handleClosePopup = () => {
    setIsPopupOpen(false);
    setPopupContent(null);
  };

  useEffect(() => {
    setNodes(cards);
  }, [cards, setNodes]);

  const nodeTypesWithHandlers = useMemo(() => ({
    customNode: (props) => <CustomNode {...props} handleShowMore={handleShowMore} />,
  }), [handleShowMore]);

  return (
    <ReactFlowProvider>
      <div style={{ width: '100%', height: '100vh', overflow: 'auto' }}>
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          onNodeDragStop={onNodeDragStop}
          fitView
          style={{ width: '100%', height: '100%' }}
          nodeTypes={nodeTypesWithHandlers}
        >
          <Background />
          <MiniMap />
          <Controls />
        </ReactFlow>
      </div>
      <Popup isOpen={isPopupOpen} onClose={handleClosePopup} content={popupContent} />
    </ReactFlowProvider>
  );
};

export default Canvas;
  